body{font-family:'Open Sans',sans-serif;font-size:18px;color:#010000;font-weight:400;font-style:normal}a{text-decoration:none;-webkit-transition:.4s ease-in-out;-o-transition:.4s ease-in-out;transition:.4s ease-in-out;cursor:pointer}a:hover{text-decoration:none;color:#ff8c00;-webkit-transition:.4s ease-in-out;-o-transition:.4s ease-in-out;transition:.4s ease-in-out}.container-fluid{line-height:24px}header{position:relative;display:block;width:100%;text-align:center;background:#010000;color:#fff;height:100px}header .countdown{position:absolute;top:50%;left:15px;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}header .logo{display:inline-block;margin:0 auto}header nav,header nav ul,header nav ul li{margin:0;padding:0}header nav{position:absolute;top:50%;right:15px;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}header nav ul li{list-style-type:none;display:inline-block;margin-right:20px}header nav ul li.last{margin:0}header nav ul li a{font-size:19;line-height:normal;color:#fff}footer{position:absolute;bottom:0;width:100%;background:#ff8c00;color:#fff;height:300px;margin-bottom:0;margin-top:100px;padding:30px}footer .links{display:inline-block;position:absolute;top:30%;left:15px;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}footer .links ul li{list-style-type:none;display:inline-block;margin-right:20px}footer .map{position:absolute;top:50%;right:20px;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);margin-right:50px}


